.workplace .ant-card,
.workplace .ant-list {
  margin-bottom: 20px;
}
.workplace .beFlex {
  display: flex;
}
.workplace .progressBox {
  padding: 20px;
  background: #fff;
}
.workplace .progressBox p {
  text-align: center;
  color: rgba(0, 0, 0, 0.45);
}
.workplace .progressBox_item {
  position: relative;
  margin-right: 50px;
  padding-right: 50px;
}
.workplace .progressBox_item:after {
  background-color: #e8e8e8;
  position: absolute;
  top: 50%;
  right: 0;
  width: 1px;
  height: 80px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  content: "";
}
.workplace .listBox {
  background: #fff;
}
.workplace .indicator > div {
  padding-top: 20px;
  padding-left: 10px;
  width: 100%;
  background: #fff;
}
.workplace .contrast > div {
  padding-top: 20px;
  padding-left: 10px;
  width: 100%;
  background: #fff;
}
.workplace .ant-list-bordered {
  border: none;
}
#workplace {
  background: transparent;
}
