.layoutComponent {
  width: 100%;
}
.layoutComponent .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}
.layoutComponent__header {
  width: 100%;
  background: #fff;
  padding: 0;
}
.layoutComponent__header .col-user {
  margin-right: 20px;
  float: right;
  line-height: 64px;
}
.layoutComponent .ant-breadcrumb {
  padding: 14px 40px 0;
}
.layoutComponent__content {
  padding: 14px 16px 0;
  width: 100%;
}
.layoutComponent__content .ant-breadcrumb {
  margin-bottom: 10px;
}
.layoutComponent__content > div {
  padding: 20px;
  width: 100%;
  min-height: 600px;
  background: #fff;
  box-sizing: border-box;
}
.layoutComponent .ant-menu-item a {
  color: #fff;
  text-decoration: none;
}
.ant-drawer-body {
  padding: 0;
}
.ant-drawer-content {
  background-color: #000818;
}
.logo {
  margin: 16px 20px 30px;
  height: 34px;
  color: #fff;
  overflow: hidden;
}
.logo img {
  width: 34px;
}
.logo span {
  display: inline-block;
  margin-left: 10px;
  font-size: 16px;
}
.userMenu span {
  margin-left: 20px;
}
.userMenu .bt {
  border-top: 1px solid #e8e8e8;
}

.login-page {
  width: 100vw;
  height: 100vh;
  background: url('data:image/jpeg;base64,');
  background-size: cover;
  position: relative;
  background-position: 50% center;
}
.login-page .ant-input-group-addon .anticon {
  display: block;
}
.login-page form .ant-input-group,
.login-page form .ant-input-group-wrappe {
  display: inline-table;
}
.login-page .login-content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -60%);
          transform: translate(-50%, -60%);
  width: 300px;
}
.login-page .login-content.login-mobile {
  left: 0;
  right: 0;
  margin: auto;
}
.login-page .login-content .ivu-card {
  font-size: 14px;
  position: relative;
  background: #ffffff;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;
}
.login-page .login-content .ivu-card-head {
  line-height: 1;
  border-bottom: 1px solid #e9eaec;
  padding: 14px 16px;
}
.login-page .login-content .ivu-card-head-inner,
.login-page .login-content .ivu-card-head p {
  display: inline-block;
  width: 100%;
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  color: #1c2438;
  font-weight: 700;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.login-page .ivu-card-body {
  padding: 6px 16px 16px;
}
.login-page .ivu-card-body .tips {
  font-size: 12px;
  text-align: center;
  color: #c3c3c3;
}

